                font-size: 16px;
                padding: 5px 0px 5px 15px;
                &::before {
                    width: 12px;
                    height: 14px;
                }
                input {
                    font-size: 16px;
                }
            }
        }
        &__all {
            padding: 7px 15px;
        }
        &__user {
            &-avatar {
                width: 70px;
            }
        }
        &__item {
            &-add {
                width: 50px;
                height: 50px;
                &::before {
                    width: 20px;
                    height: 15px;
                }
            }
        }
        &__checked {
            width: 50px;
            height: 50px;
            &::before {
                width: 20px;
                height: 15px;
            }
        }
    }
}

