.Account {
    width: 40%;
    margin-left: auto;
    &__form {
        display: flex;
        flex-direction: column;
        label {
            color: white;
            display: flex;
            flex-direction: column;
            line-height: 38px;
        }
        input,
        button {
            padding: 22px 40px;
            border-radius: 40px;
            color: white;
            font-size: 18px;
            font-family: "Bellota Text";
        }
        input {
            background: rgba(#ffffff, 0.1);
            &::placeholder {
                font-size: 18px;
                font-family: "BellotaText";
                letter-spacing: 0.5px;
            }
        }
        button {
            margin-top: 30px;
            background-color: hsl(61, 100%, 72%);
            color: #1d1d1d;
            font-family: "BellotaText";
            font-weight: 700;
            font-size: 18px;
        }
    }
}
