.Payouts {
    width: 40%;
    &__inner {
    }
    &__items {
    }
    &__date {
        color: white;
        margin-bottom: 15px;
    }
    &__item {
        padding: 20px 40px;
        background-color: #050505;
        border-radius: 10px;
        margin-bottom: 12px;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__info {
        &-title {
            font-style: italic;
            font-size: 16px;
            margin-bottom: 30px;
            color: #a0a0a0;
        }
        &-name {
            color: #d7d7d7;
        }
    }
    &__cash {
        &-money {
            font-family: Baloo;
            font-size: 28px;
            line-height: 38px;
            letter-spacing: 0.5px;
            color: #fcff6e;
            margin-bottom: 10px;
        }
        &-more {
            a {
                letter-spacing: 0.5px;
                color: #c4c4c4;
                font-size: 16px;
            }
        }
    }
}
