.Header {
    &__inner {
        padding: 0 30px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
    }
    &__logo {
        max-width: 30%;
    }
    &__nav {
        &-list {
            display: flex;
        }
        &-li {
            margin: 0 22px;
            a {
                color: white;
                &.active,
                &:hover {
                    position: relative;
                    &::after {
                        content: "";
                        position: absolute;
                        display: block;
                        top: 10px;
                        left: -20%;
                        width: 140%;
                        height: 100%;
                        background-color: rgba(#7bffa0, 0.1);
                    }
                }
            }
        }
    }
}
