.AddIndited {
    &__inner {
        width: 50%;
        padding: 61px 84px;
        background-color: black;
    }
    &__form {
        &-title {
            color: #bebebe;
            margin-bottom: 52px;
        }
        &-input {
            label {
                display: flex;
                flex-direction: column;
                margin-bottom: 25px;
                input {
                    margin-top: 5px;
                    background-color: transparent;
                    border: 1px solid #555555;
                    border-radius: 8px;
                    padding: 14px 22px;
                    font-size: 16px;
                    color: white;
                    &::placeholder {
                        font-size: 16px;
                    }
                }
            }
        }
        &-submit {
            width: 100%;
            display: block;
            background-color: #ebee6b;
            padding: 20px 57px;
            border-radius: 8px;
            font-weight: 700;
            font-size: 18px;
            margin: 0 auto;
            margin-bottom: 20px;
        }
        &-link {
            width: 100%;
            margin: 0 auto;
            text-align: center;
            background-color: rgba(#74eed8, 0.6);
            padding: 20px 57px;
            color: #d2d2d2;
            border-radius: 8px;
        }
    }
}
