.Cpa {
    &__inner {
    }

    &__items {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
    }
    &__item,
    &__add {
        width: calc(90% / 3);
        background-color: rgba(black, 0.8);
        border-radius: 10px;
        overflow: hidden;
        margin: 10px;
        min-height: 300px;
        &-inner {
            padding: 34px 44px;
        }
        &-title {
            color: white;
            margin-bottom: 18px;
        }
        &-date {
            font-size: 16px;
            letter-spacing: 0.5px;
            margin-bottom: 20px;
        }
        &-text {
            font-style: italic;
            line-height: 38px;
            font-size: 16px;
            margin-bottom: 24px;
            font-weight: 700;
        }
        &-price {
            text-align: right;
            font-family: Baloo;
            font-weight: 400;
            font-size: 42px;
            line-height: 38px;
            letter-spacing: 0.5px;
            color: rgba(94, 255, 226, 0.9);
        }
    }
    &__add {
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        &-add {
            width: calc(905 / 3);
            border-radius: 50%;
            padding: 5px;
            border: 4px solid rgba(#fcff7b, 0.7);
            cursor: pointer;
            display: flex;
            align-items: center;
            justify-content: center;
            &::before {
                content: "";
                display: block;
                width: 30px;
                height: 30px;
                background-image: url("../../assets/image/plus.svg");
                background-position: center;
                background-size: cover;
                background-repeat: no-repeat;
            }
        }
    }
}
