.SignIn {
    max-width: 500px;
    font-size: 18px;
    margin-top: 20px;

    &__form {
        display: flex;
        flex-direction: column;
        align-items: center;
        input,
        button {
            width: 100%;
            padding: 22px 40px;
            border-radius: 40px;
            margin-bottom: 35px;
            color: white;
            font-size: 18px;
            font-family: "Bellota Text";
        }
        input {
            background: rgba(#ffffff, 0.1);
            &::placeholder {
                font-size: 18px;
                font-family: "BellotaText";
                letter-spacing: 0.5px;
            }
        }
        button {
            background-color: hsl(61, 100%, 72%);
            color: #1d1d1d;
            font-family: "BellotaText";
            font-weight: 700;
            font-size: 18px;
        }
    }
    &__youtube {
        &-span {
            display: inline-block;
            color: white;
            font-size: 18px;
            margin-bottom: 10px;
        }
        &-btn {
            padding: 22px 40px;
            border-radius: 40px;
            text-align: center;
            background-color: black;
            color: white;
            font-family: "BigShouldersDisplay";
            font-size: 24px;
            cursor: pointer;
            &::before {
                content: "";
                display: inline-block;
                width: 45px;
                height: 20px;
                border-radius: 8px;
                margin-right: 5px;
                background-image: url("../../assets/image/youtube.svg");
                background-position: center;
                background-repeat: no-repeat;
                background-size: cover;
            }
        }
    }
}
