.UsersInviteds {
    &__inner {
        background-color: rgba(#030303, 0.8);
        border-radius: 10px;
        padding: 44px;
    }
    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__search {
        display: flex;
        &-filter {
            padding: 10px 20px;
            border: 1px solid #5b5b5b;
            border-radius: 10px;
            color: #424242;
            margin-right: 15px;
            display: flex;
            align-items: center;
            font-size: 18px;
            cursor: pointer;
        }
        &-search {
            display: flex;
            align-items: center;
            padding: 10px 10px 10px 30px;
            border: 1px solid #5b5b5b;
            border-radius: 10px;
            position: relative;
            &::before {
                content: "";
                position: absolute;
                display: inline-block;
                left: 10px;
                width: 15px;
                height: 18px;
                background-image: url("../../assets/image/search.svg");
                background-position: center;
                background-size: cover;
                background-repeat: no-repeat;
                margin-right: 10px;
            }
            input {
                color: white;
                background: transparent;
                font-size: 18px;
                margin-left: 10px;
                &::placeholder {
                    font-size: 18px;
                }
            }
        }
    }
    &__all {
        padding: 14px 30px;
        background: rgba(#91dce1, 0.2);
        border-radius: 10px;
        color: #c7c7c7;
        cursor: pointer;
    }
    &__items {
        margin-top: 62px;
        height: 400px;
        overflow: auto;
        padding-right: 10px;
        background-color: black;
    }
    &__item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 30px;
        padding: 10px;
        background-color: rgba(#bae2eb, 0.2);
        border-radius: 8px;
        cursor: pointer;
        &.invited {
            background-color: transparent;
            .UsersInviteds {
                &__user {
                    &-name {
                        color: #878787;
                        &::after {
                            content: "Приглашен";
                            position: absolute;
                            top: -50%;
                            right: -1%;
                            font-size: 12px;
                            color: white;
                            text-transform: uppercase;
                            padding: 5px;
                            background-color: rgba(#ffffff, 0.2);
                        }
                    }
                    &-email {
                        color: #c4c4c4;
                    }
                }
                &__checked {
                    background: rgba(#fcff7b, 0.2);
                    &::before {
                        background-image: url("../../assets/image/check.svg");
                    }
                }
            }
        }
        &:last-child {
            background-color: transparent;
        }
        &-add {
            margin-left: auto;
            width: 70px;
            height: 70px;
            border-radius: 50%;
            border: 4px solid rgba(#fcff7b, 0.7);
            cursor: pointer;
            display: flex;
            align-items: center;
            justify-content: center;
            &::before {
                content: "";
                display: block;
                width: 30px;
                height: 30px;
                background-image: url("../../assets/image/plus.svg");
                background-position: center;
                background-size: cover;
                background-repeat: no-repeat;
            }
        }
    }
    &__user {
        display: flex;
        align-items: center;
        &-avatar {
            margin-right: 30px;
            width: 90px;
        }
        &-name {
            color: white;
            margin-bottom: 23px;
            position: relative;
        }
        &-email {
            color: white;
        }
    }
    &__checked {
        width: 70px;
        height: 70px;
        border-radius: 50%;
        background: #54e5cb;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        &::before {
            content: "";
            display: block;
            width: 31px;
            height: 25px;
            background-image: url("../../assets/image/check_white.svg");
            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;
        }
    }
    &__invite {
        display: flex;
        max-width: 260px;
        align-items: center;
        justify-content: center;
        margin: 0 auto;
        background-color: #ebee6b;
        padding: 20px 10px;
        border-radius: 10px;
        color: #2f2f2f;
        cursor: pointer;
        margin-top: 10px;
    }
}
