.Statistics {
    margin-bottom: 10px;
    &__inner {
        border-radius: 10px;
        background-color: black;
        display: flex;
        justify-content: space-between;
    }
    &__views,
    &__month {
        padding: 32px 40px;
        width: 50%;
        display: flex;
        justify-content: space-between;
    }
    &__views {
        position: relative;
        &::before {
            content: '';
            position: absolute;
            right: 0;
            top: 10%;
            height: 80%;
            width: 3px;
            background-color: #242424;
        }
    }
    &__icon {
        width: 30px;
    }
    &__info {
        text-align: right;
    }
    &__count {
        color: #fcff7b;
        font-family: "Baloo";
        font-size: 28px;
        margin-bottom: 5px;
    }
    &__text {
        color: #ddfca4;
        font-size: 18px;
    }
}
