.Bar {
    &__inner {
    }
    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 18px;
        margin-bottom: 20px;
        &-month {
            cursor: pointer;
            min-width: 200px;
            background-color: #1b1b1b;
            text-align: center;
            border-radius: 10px;
            padding: 14px 20px;
            position: relative;
            display: flex;
            justify-content: space-between;
            align-items: center;
            position: relative;
            &::after {
                content: "";
                display: inline-block;
                width: 20px;
                height: 25px;
                background-image: url("../../../assets/image/arrows.svg");
                background-position: center;
                background-size: cover;
                background-repeat: no-repeat;
            }
            ul {
                position: absolute;
                top: 45px;
                left: 0;
                max-height: 200px;
                overflow: auto;
                border-radius: 10px;
                li {
                    cursor: pointer;
                    min-width: 200px;
                    background-color: #1b1b1b;
                    padding: 15px 0;
                    &:hover {
                        background-color: lighten(#1b1b1b, 10);
                    }
                }
            }
        }
    }
    &__main {
        padding: 20px;
        background-image: url("../../../assets/image/сетка.png");
        background-repeat: no-repeat;
        background-position: bottom;
        height: 300px;
    }
}
